.App {
  height: 100vh;
  width: 100vw;
  background: linear-gradient(-45deg, #c3624d, #c13661, #237ba8, #1d9674);
  background-size: 400% 400%;
  animation: gradient 30s ease infinite;
  color: white;
}

.makeStyles-aboutButton-3.MuiButton-root {
  color: white;
}

@keyframes gradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}

.MuiButton-label {
  font-family: Comfortaa, cursive;
}
